import Tab2 from '@/components/Tab2'
import Tab3 from '@/components/Tab3'
import Tab4 from '@/components/Tab4'
import Wakura from '@/components/Wakura'

Vue.config.productionTip = false; // ÈñãÁô∫‰∏≠„ÅØ true „Å´„Åó„Åü„Åª„ÅÜ„Åå„ÅÑ„ÅÑ„Åã„ÇÇ
// true „ÅÆÂ†¥Âêà„ÅÆÊñπ„ÅåÈñãÁô∫ËÄÖÂêë„Åë„ÅÆ„É°„ÉÉ„Çª„Éº„Ç∏„Åå„Ç≥„É≥„ÇΩ„Éº„É´„Å´„Çà„Çä„Åü„Åè„Åï„ÇìÂá∫„Çã„Çà„ÅÜ„Åß„Åô„ÄÇ
Vue.use(VueRouter)
